.project-container {
  position: relative;
  width: 100%;
  height: calc(100vh + 20px);
  margin: 20px;
}

.project-title {
  position: absolute;
  height: 500px;
  width: 500px;
  top: 50%;
  translate: 0% -50%;
  left: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-name {
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-family: "futura_m";
  font-weight: normal;
  text-shadow: 0 10px 30px rgb(2 11 22 / 50%);
  font-size: 5rem;
}

.project-img {
  position: absolute;
  top: 50%;
  right: 5%;
  translate: 0% -50%;
  height: 400px;
  width: 600px;
  background-color: #072041;
}

img {
  object-fit: cover;
}

.project-image {
  position: absolute;
  top: 0%;
  bottom: 0%;
  width: 100%;
  right: 0%;
  left: 0%;
  background-color: rgba(255, 85, 96, 0.2);
  z-index: 4;
}

.project-i {
  position: absolute;
  top: 0%;
  bottom: 0%;
  width: 100%;
  left: 0%;
  background-color: rgba(255, 85, 96, 0.2);
  z-index: 5;
}

button {
  cursor: pointer;
}

.project-btn {
  margin-top: 20px;
  font-size: 1.8rem;
  width: fit-content;
  background-color: #ff4d5a;
  height: fit-content;
  padding: 10px;
  border: none;
  color: white;
  border-radius: 999px;
}

.p-img {
  position: absolute;
  height: 100%;
  width: 100%;
}

@media (max-width: 950px) {
  .project-name {
    font-size: 4rem;
  }
}

@media (max-width: 850px) {
  .project-name {
    font-size: 3rem;
  }
  .project-btn {
    font-size: 1.2rem;
  }
}

@media (max-width: 790px) {
  .project-img {
    height: 300px;
    width: 450px;
  }
  .project-title {
    font-size: 1.2rem;
  }
}

